import { Component, OnInit,Input, ViewEncapsulation} from '@angular/core';
import { ModalController } from '@ionic/angular';


@Component({
  selector: 'app-modaldonacion',
  templateUrl: './modaldonacion.component.html',
  styleUrls: ['./modaldonacion.component.scss'],
  encapsulation:ViewEncapsulation.None,
})
export class ModaldonacionComponent implements OnInit {
  @Input() tokenID;
  @Input() saldo;
  importe:number=0;
  texto:any="";
  error:Boolean=false;
  textoError:any="";
  constructor(private modalCtrl: ModalController) { }

  ngOnInit() {
  }

  cambiaCantidad(event) {
    this.importe = event.target.value;
  }

  cambiaTexto(event) {
    this.texto = event.target.value;
  }

  salir(){
    this.modalCtrl.dismiss({
      orden:'cancelar'
    });
  }
  confirmar(){

    if (this.importe<=0)
    {
    this.error=true;
    this.textoError="¿La donación debe ser superior 0 no crees? ;)";
    return;
    }

    if (this.importe>this.saldo)
    {
    this.error=true;
    this.textoError="Se agredece el entusiasmo, pero no tienes suficiente saldo en tu Wallet";
    return;
    }

    this.modalCtrl.dismiss({
      orden:'confirmar',
      importe:this.importe,
      texto:this.texto
    });
  

  
  
  }

}
