import { Component, Input, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { ViewChild,ElementRef } from '@angular/core';


@Component({
  selector: 'app-modal-mint',
  templateUrl: './modal-mint.component.html',
  styleUrls: ['./modal-mint.component.scss'],
})
export class ModalMintComponent implements OnInit {
  @Input() nombre;
  @Input() descripcion;
  @Input() canvas;
  @ViewChild('imgCanvas') imgCanvas: ElementRef;

  imgCanvasAux:any;

  constructor(private modalCtrl: ModalController) { }

  ngOnInit() {
    
  }

  ngAfterViewInit(): void {

    this.imgCanvasAux=this.imgCanvas.nativeElement;
    var origenCtx=this.canvas.getContext('2d');
    this.imgCanvasAux.src= this.canvas.toDataURL("image/jpg");
 

  }

  salir(){
    this.modalCtrl.dismiss({
      orden:'cancelar'
    });
  }
  confirmar(){
    
    this.modalCtrl.dismiss({
      orden:'confirmar'
    });
  
  }

}
